import { Injectable } from '@angular/core';
import { NavigatorProvider } from '@nequi/features/commons';
import { AlertService } from '@nequi/features/commons/presentation';
import { openUrl } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { TokenValidationConfig } from './token-validation.config';

interface AlertMessages {
  leftButton: string;
  rightButton?: string;
  leftAction?: () => void;
  rightAction?: () => void;
  title: string;
  content: string;
}

@Injectable()
export class TokenValidationService {
  constructor(private navigatorProvider: NavigatorProvider, private alertService: AlertService) {}

  public showErrorAlert(messages: AlertMessages, action?: () => void, cancelAction?: () => void) {
    const buttons = { accept: messages.leftButton, cancel: messages.rightButton };

    this.alertService.show({
      messages: {
        header: messages.title,
        message: messages.content,
        buttons,
      },
      handlers: {
        accept: action,
        cancel: cancelAction,
      },
    });
  }

  public showAlertExceedTokenAttempts(exceededTokenAttempts: any) {
    this.showErrorAlert(exceededTokenAttempts, () => this.goBack());
  }

  public showAlertNoNotificationsInscribed(noNotificationsInscribed: any) {
    this.showErrorAlert(
      noNotificationsInscribed,
      () => this.goBack(),
      () => {
        openUrl(TokenValidationConfig.helpLinkNotifications);
      }
    );
  }

  public showAlertWrongToken(wrongToken: any) {
    this.showErrorAlert(wrongToken);
  }

  public showAlertExpiredToken(expiredToken: any) {
    this.showErrorAlert(expiredToken);
  }

  public showAlertInvalidOtp(isThatYourKey: any, attemptsRemaining: string) {
    const alertCopy = {
      ...isThatYourKey,
      content: createFormat(isThatYourKey.content, [attemptsRemaining]),
    };

    this.showErrorAlert(alertCopy);
  }

  public showAlertUserExceedAttempts(attemptsBlock, blockRemaining: string) {
    const alertCopy = {
      ...attemptsBlock,
      content: createFormat(attemptsBlock.content, [blockRemaining]),
    };

    this.showErrorAlert(alertCopy, () => this.goToCashInHome());
  }

  public showAlertDynamicTokenDisabled(weLackMagic: any) {
    this.showErrorAlert(weLackMagic, () => this.goToCashInHome());
  }

  public showAlertSharedKeyNotFound(andTheMagicKey: any) {
    this.showErrorAlert(andTheMagicKey);
  }

  public showAlertClientManualBlock(blockedGenius: any) {
    this.showErrorAlert(blockedGenius, () => this.goToCashInHome());
  }

  public showAlertGeniusNotAvailable(geniusNotAvailable, errorMessage: string) {
    const alertCopy = {
      ...geniusNotAvailable,
      content: errorMessage,
    };

    this.showErrorAlert(alertCopy);
  }

  public showAlertLinkAccountFailed() {
    this.alertService.showByI18n({ i18nKey: TokenValidationConfig.i18n.linkAccountFail });
  }

  public async goToRecharge(): Promise<void> {
    await this.navigatorProvider.navigateRoot(TokenValidationConfig.routes.backpack);
    await this.navigatorProvider.navigateForward(TokenValidationConfig.routes.bankRecharge);
  }

  public async goToCashInHome() {
    await this.navigatorProvider.navigateRoot(TokenValidationConfig.routes.backpack);
    await this.navigatorProvider.navigateForward(TokenValidationConfig.routes.cashIn);
  }

  public goBack() {
    this.navigatorProvider.back();
  }
}
