import { BankAccountEntity } from '@nequi/features/commons/core';

import { TokenValidationConfig } from './token-validation.config';

export class TokenValidationViewModel {
  public size = TokenValidationConfig.passwordSize;
  public token = '';
  public isValidatingToken: boolean;
  public exceededTokenAttempts = false;
  public bankAccount: BankAccountEntity;

  public clearBankAccount() {
    this.bankAccount = {
      // eslint-disable-next-line id-blacklist
      number: '',
      type: null,
    };
  }
}
