import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import { ErrorCode, ModuleConfigProvider } from '@nequi/features/commons';
import {
  AnalyticsInteractor,
  AnalyticsType,
  Attribute,
  ParameterInteractor,
  Transaction
} from '@nequi/features/commons/core';
import { AlertParameters, TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  BankRechargeLimitsEntity,
  BankRechargeTransaction,
  ProcessType,
  SendInteractor
} from '@nequi/features/send/core';
import { CardModalService, ConfirmInfoPocketProps, MaskSensitivePipe } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';
import { TranslatePipe } from '@ngx-translate/core';

import { HomeConfig } from './home.config';
import { HomeViewModel } from './home.view-model';

const errorMessages = {
  [ErrorCode.BANK_RECHARGE.ACCOUNT_WITH_LOCKS]: 'bank-recharge.home.errors.locked-account',
  [ErrorCode.BANK_RECHARGE.ACCOUNT_WITH_LOCKS_SECOD_CASE]:
    'bank-recharge.home.errors.locked-account',
  [ErrorCode.BANK_RECHARGE.DEBIT_LIMIT_REACHED]: 'bank-recharge.home.errors.limits-reached',
  [ErrorCode.BANK_RECHARGE.MONTHLY_DEBIT_LIMIT_REACHED]:
    'bank-recharge.home.errors.monthly-limits-reached',
  [ErrorCode.BANK_RECHARGE.NEQUI_ACCOUNT_LOCKED]: 'bank-recharge.home.errors.nequi-account-locked',
  [ErrorCode.BANK_RECHARGE.UNAVAILABLE_ACCOUNT]: 'bank-recharge.home.errors.unavailable-account',
  [ErrorCode.BANK_RECHARGE.EXCEEDS_CEILING]: 'bank-recharge.home.errors.exceeds-ceiling',
  [ErrorCode.BANK_RECHARGE.NOT_BALANCE_AVAILABLE]: 'bank-recharge.home.errors.no-balance-available',
};

@Component({
  selector: 'app-home-recharge',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
  providers: [MaskSensitivePipe],
})
export class HomePage extends TransactionBasePage implements OnInit {
  public viewModel: HomeViewModel;
  public isLoading = false;
  public busyIndicatorError: string;

  constructor(
    private sendInteractor: SendInteractor,
    private parameterInteractor: ParameterInteractor,
    private route: ActivatedRoute,
    private modalService: CardModalService,
    private translatePipe: TranslatePipe,
    private currencyPipe: CurrencyPipe,
    private analyticsInteractor: AnalyticsInteractor,
    private moduleConfigProvider: ModuleConfigProvider,
    sensitivePipe: MaskSensitivePipe,
    injector: Injector
  ) {
    super(injector);

    this.viewModel = new HomeViewModel(sensitivePipe, this.translateProvider);
  }

  public async ngOnInit(): Promise<void> {
    await this.translateProvider.loadModule(this.moduleConfigProvider.i18n.bankRechargeModuleKey);
  }

  public async ionViewWillEnter() {
    await this.initView();
  }

  public async initView() {
    this.isLoading = true;

    try {
      this.viewModel.initForm();
      const limits = (
        await this.parameterInteractor.getParameter<Attribute[]>(HomeConfig.rechargeLimits)
      ).reduce((all, param) => {
        return {
          ...all,
          [param.code]: param.value,
        };
      }, {} as BankRechargeLimitsEntity);

      this.viewModel.setLimits(limits);

      const accounts = await this.sendInteractor.get(ProcessType.LINK_ACCOUNTS);

      this.viewModel.setAccounts(accounts);

      this.isLoading = false;
    } catch (error) {
      this.busyIndicatorError = error.message;
    }
  }

  public async recharge(): Promise<void> {
    const transaction = new BankRechargeTransaction()
      .withAmount(this.viewModel.amount.value)
      .withData(this.viewModel.account.value);

    super.executeTransaction(transaction);
  }

  public async addAccount(): Promise<void> {
    if (!this.viewModel.hasAccountsToLink) {
      const messages = this.translateProvider.get(HomeConfig.modalMessages);

      this.modalService.show({
        ...messages,
        imgPath: messages.image,
        buttons: [
          {
            text: messages.okButton,
          },
        ],
      });
    } else {
      this.navigatorProvider.navigateForward([HomeConfig.linkAccountRoute], {
        relativeTo: this.route,
      });
    }
  }

  public goToHelp() {
    const url = this.translateProvider.get(HomeConfig.routes.helpLink);

    openUrl(url);
  }

  protected async setOriginMoney(transaction: Transaction) {
    return transaction.withPocket(null);
  }

  protected async getConfirmationInfo(): Promise<ConfirmInfoPocketProps> {
    const messages = this.translateProvider.get(HomeConfig.i18n.confirmation);

    return {
      title: messages.title,
      items: [
        { label: messages.items.account, value: this.viewModel.selectedAccount.maskedNumber },
        {
          label: messages.items['account-type'],
          value: this.viewModel.selectedAccount.visibleType,
        },
        {
          label: messages.items.amount,
          value: this.currencyPipe.transform(this.viewModel.amount.value),
        },
      ],
      confirmLabel: messages.buttons.confirm,
      cancelLabel: messages.buttons.cancel,
    };
  }

  protected onTransactionSuccess(_response?: unknown, _transaction?: Transaction) {
    this.setAnalytics();
    this.navigatorProvider.navigateRoot(HomeConfig.routes.admin);
  }

  protected async onTransactionFailed(
    error: CoreException<{ remainingBalance: number }>
  ): Promise<void> {
    const messages = this.translateProvider.get(
      errorMessages[error.code] ?? HomeConfig.i18n.errorDefaults
    );

    const alertOptions: AlertParameters = {
      messages: {
        header: messages.title,
        message: messages.content,
        buttons: {
          accept: messages.buttons?.okButton,
          cancel: messages.buttons?.cancelButton,
        },
      },
    };

    switch (error.code) {
      case ErrorCode.BANK_RECHARGE.MONTHLY_DEBIT_LIMIT_REACHED:
        alertOptions.messages.message = this.translatePipe.transform(
          `${errorMessages[error.code]}.content`,
          { remaining: this.currencyPipe.transform(error.data.remainingBalance) }
        );

        break;
      case ErrorCode.BANK_RECHARGE.EXCEEDS_CEILING:
        alertOptions.handlers = {
          accept: () =>
            this.navigatorProvider.navigateForward(HomeConfig.routes.breakCeiling),
        };

        break;
    }

    this.alertService.show(alertOptions);
  }

  private setAnalytics() {
    const analytics = HomeConfig.analytics.events;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: analytics.successfulRecharge },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: analytics.successfulRechargeFb },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: analytics.successfulRecharge },
    });
  }
}
