import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import { BankAccountEntity } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  ModuleConfigProvider,
  TranslateProvider,
  UserInfoProvider
} from '@nequi/features/commons/providers';
import { PinChallengeModalService } from '@nequi/features/security/auth/presentation';
import {
  ClientManualBlockException,
  DynamicTokenDisabledException,
  ExceededTokenAttemptsException,
  ExpiredTokenException,
  InvalidOtpException,
  NoNotificationsInscribedException,
  SendInteractor,
  SharedKeyNotFoundException,
  UserExceedAttemptsException,
  WrongTokenException
} from '@nequi/features/send/core';
import { CardModalService, ConfirmInfoBottomSheetService, LoadingService } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';

import { TokenValidationConfig } from './token-validation.config';
import { TokenValidationService } from './token-validation.service';
import { TokenValidationViewModel } from './token-validation.view-model';

@Component({
  selector: 'app-token-validation',
  templateUrl: './token-validation.page.html',
  providers: [TokenValidationService],
})
export class TokenValidationPage extends FeatureBasePage implements OnInit {
  public viewModel: TokenValidationViewModel;

  constructor(
    private router: Router,
    private pinChallengeModalService: PinChallengeModalService,
    private translateProvider: TranslateProvider,
    private loadingService: LoadingService,
    private cardModalService: CardModalService,
    private confirmBottomSheetService: ConfirmInfoBottomSheetService,
    private sendInteractor: SendInteractor,
    private tokenValidationService: TokenValidationService,
    private userInfoProvider: UserInfoProvider,
    private moduleConfigProvider: ModuleConfigProvider,
    injector: Injector
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.bankRechargeModuleKey);
  }

  public ngOnInit() {
    this.viewModel = new TokenValidationViewModel();

    this.viewModel.bankAccount = this.router.getCurrentNavigation().extras.state
      .bankAccount as BankAccountEntity;
  }

  public async confirmGoBack() {
    if (this.viewModel.exceededTokenAttempts) {
      const exceededTokenAttempts = this.translateProvider.get(
        TokenValidationConfig.i18n.exceededTokenAttempts
      );

      this.tokenValidationService.showAlertExceedTokenAttempts(exceededTokenAttempts);

      return;
    }

    await this.showModalHowToDoIt();
  }

  public async onFilled() {
    try {
      await this.loadingService.show();
      await this.sendInteractor.validateToken(this.viewModel.token);
      await this.loadingService.hide(true);

      this.showBottomSheetConfirmation();
    } catch (error) {
      await this.loadingService.hide(false);
      this.manageErrors(error);
    }
  }

  private async showModalHowToDoIt() {
    const messages = this.translateProvider.get(TokenValidationConfig.i18n.howToDoItModal);

    this.cardModalService.show({
      imgPath: TokenValidationConfig.modalImage,
      title: messages.title,
      content: messages.content,
      buttons: [
        {
          text: messages.helpLinkText,
          color: 'guayaba',
          handler: () => {
            openUrl(messages.helpLink);
          },
        },
        {
          text: messages.exit,
          color: 'medianoche',
          handler: () => this.tokenValidationService.goBack(),
        },
      ],
    });
  }

  private async showBottomSheetConfirmation() {
    const accountTypes = this.translateProvider.get(TokenValidationConfig.i18n.accountTypes);

    const messages = this.translateProvider.get(TokenValidationConfig.i18n.confirmBottomSheet);

    const { data: isConfirmed } = await this.confirmBottomSheetService.show({
      title: messages.title,
      confirmLabel: messages.confirm,
      cancelLabel: messages.cancel,
      items: [
        {
          label: messages.accountType,
          value: accountTypes[this.viewModel.bankAccount.type],
        },
        {
          label: messages.accountNumber,
          value: this.viewModel.bankAccount.number,
        },
      ],
    });

    if (!isConfirmed) {
      return;
    }

    this.processPinChallenge();
  }

  private async processPinChallenge() {
    const messages = this.translateProvider.get(TokenValidationConfig.i18n.pinChallenge);

    if (this.userInfoProvider.getSecurity().value) {
      this.linkAccount();

      return;
    }

    const isValid = await this.pinChallengeModalService.show({
      title: messages.title,
      message: messages.message,
    });

    if (isValid) {
      this.linkAccount();
    }
  }

  private async linkAccount() {
    await this.loadingService.show();

    try {
      await this.sendInteractor.link(this.viewModel.bankAccount);
      await this.loadingService.hide(true);

      this.viewModel.clearBankAccount();
      this.showModalLinkAccountSuccess();
    } catch (error) {
      await this.loadingService.hide(false);
      this.tokenValidationService.showAlertLinkAccountFailed();
    }
  }

  private async showModalLinkAccountSuccess() {
    this.setCustomGoBack(this.tokenValidationService.goToCashInHome);

    const messages = this.translateProvider.get(TokenValidationConfig.i18n.linkAccountSuccess);

    this.cardModalService.show({
      imgPath: TokenValidationConfig.lampIconPath,
      title: messages.title,
      content: messages.content,
      buttons: [
        {
          text: messages.ready,
          color: 'guayaba',
          handler: async () => {
            await this.loadingService.show();
            await this.tokenValidationService.goToRecharge();
            this.clearCustomGoBack();
            this.loadingService.hideWithoutFeedback();
          },
        },
      ],
    });
  }

  private async manageErrors(error: CoreException<any>) {
    this.viewModel.token = '';
    this.viewModel.isValidatingToken = false;

    const tokenValidationMessages = this.translateProvider.get(
      TokenValidationConfig.i18n.tokenValidation
    );

    switch (error.constructor) {
      case ExceededTokenAttemptsException:
        this.viewModel.exceededTokenAttempts = false;

        this.tokenValidationService.showAlertExceedTokenAttempts(
          tokenValidationMessages.exceededTokenAttempts
        );

        break;
      case NoNotificationsInscribedException:
        this.tokenValidationService.showAlertNoNotificationsInscribed(
          tokenValidationMessages.noNotificationsInscribed
        );

        break;
      case WrongTokenException:
        this.tokenValidationService.showAlertWrongToken(tokenValidationMessages.wrongToken);

        break;
      case ExpiredTokenException:
        this.tokenValidationService.showAlertExpiredToken(tokenValidationMessages.expiredToken);

        break;
      case InvalidOtpException:
        this.tokenValidationService.showAlertInvalidOtp(
          tokenValidationMessages.isThatYourKey,
          error.data.attemptsRemaining
        );

        break;
      case UserExceedAttemptsException:
        this.tokenValidationService.showAlertUserExceedAttempts(
          tokenValidationMessages.attemptsBlock,
          error.data.blockRemaining
        );

        break;
      case DynamicTokenDisabledException:
        this.tokenValidationService.showAlertDynamicTokenDisabled(
          tokenValidationMessages.weLackMagic
        );

        break;
      case SharedKeyNotFoundException:
        this.tokenValidationService.showAlertSharedKeyNotFound(
          tokenValidationMessages.andTheMagicKey
        );

        break;
      case ClientManualBlockException:
        this.tokenValidationService.showAlertClientManualBlock(
          tokenValidationMessages.blockedGenius
        );

        break;
      default:
        this.tokenValidationService.showAlertGeniusNotAvailable(
          tokenValidationMessages.geniusNotAvailable,
          error.message
        );

        break;
    }
  }
}
