import { FormControl, FormGroup, Validators } from '@angular/forms';
import { BankAccountEntity } from '@nequi/features/commons/core';
import { clearFormErrors } from '@nequi/features/commons/presentation';

import { LinkBankAccountConfig } from './link-bank-account.config';

export class LinkBankAccountViewModel {
  public form: FormGroup;
  public accountNumberFormControl: FormControl;
  public accountTypeOptions = [];

  public initForm() {
    this.createFormGroup();
  }

  public getBankAccount(): BankAccountEntity {
    return {
      type: this.form.value.accountType.alias,
      // eslint-disable-next-line id-blacklist
      number: this.form.value.accountNumber,
    };
  }

  private createFormGroup() {
    this.accountNumberFormControl = new FormControl(null, [
      Validators.minLength(LinkBankAccountConfig.validators.minLength),
      Validators.maxLength(LinkBankAccountConfig.validators.maxLength),
      Validators.pattern(LinkBankAccountConfig.validators.pattern),
      Validators.required,
    ]);

    this.form = new FormGroup({
      accountType: new FormControl(null, [Validators.required]),
      accountNumber: this.accountNumberFormControl,
    });

    clearFormErrors(this.form);
  }
}
