import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { HomePage } from './home/home.page';
import { LinkBankAccountPage } from './link-bank-account/link-bank-account.page';
import { TokenValidationPage } from './token-validation/token-validation.page';

const routes: Routes = [
  {
    path: '',
    component: HomePage,
  },
  {
    path: 'link-bank-account',
    component: LinkBankAccountPage,
  },
  {
    path: 'token-validation',
    component: TokenValidationPage,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class BankRechargeRoutingModule {}
