import { Component, OnInit } from '@angular/core';
import { AccountType } from '@nequi/features/commons/core';
import { AlertService } from '@nequi/features/commons/presentation';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import { SendInteractor } from '@nequi/features/send/core';
import { CardModalService } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';

import { LinkBankAccountConfig } from './link-bank-account.config';
import { LinkBankAccountViewModel } from './link-bank-account.view-model';

@Component({
  selector: 'app-link-bank-account',
  templateUrl: './link-bank-account.page.html',
})
export class LinkBankAccountPage implements OnInit {
  public viewModel = new LinkBankAccountViewModel();

  constructor(
    private navigatorProvider: NavigatorProvider,
    private translateProvider: TranslateProvider,
    private alertService: AlertService,
    private cardModalService: CardModalService,
    private sendInteractor: SendInteractor,
    private moduleConfigProvider: ModuleConfigProvider
  ) {
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.bankRechargeModuleKey).then(() => {
      this.loadAccountTypeOptions();
    });
  }

  public async ngOnInit() {
    this.viewModel.initForm();
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public async validateBankAccount() {
    const bankAccount = this.viewModel.getBankAccount();
    const isLinked = await this.sendInteractor.isLinked(bankAccount);

    if (isLinked) {
      await this.showModalIsLinked();

      return;
    }

    const isUnlinked = await this.sendInteractor.isUnlinked(bankAccount);

    if (isUnlinked) {
      this.showTokenModal();

      return;
    }

    await this.showAlertIsUnlinked();
  }

  private async showAlertIsUnlinked() {
    const messages = this.translateProvider.get(LinkBankAccountConfig.i18n.isUnlinkedAlert);

    this.alertService.show({
      messages: {
        header: messages.title,
        message: messages.content,
        buttons: { accept: messages.ready },
      },
    });
  }

  private async showTokenModal() {
    const messages = this.translateProvider.get(LinkBankAccountConfig.i18n.tokenModal);

    this.cardModalService.show({
      imgPath: LinkBankAccountConfig.modalImage,
      title: messages.title,
      content: messages.content,
      buttons: [
        {
          text: messages.ready,
          handler: () => {
            this.navigatorProvider.navigateForward([LinkBankAccountConfig.tokenValidationUrl], {
              state: {
                bankAccount: this.viewModel.getBankAccount(),
              },
            });
          },
        },
        {
          text: messages.helpLinkText,
          isLink: true,
          handler: () => {
            openUrl(messages.helpLink);
          },
        },
      ],
    });
  }

  private async showModalIsLinked() {
    const messages = this.translateProvider.get(LinkBankAccountConfig.i18n.isLinkedModal);

    this.cardModalService.show({
      imgPath: LinkBankAccountConfig.modalImage,
      title: messages.title,
      content: messages.content,
      buttons: [
        {
          text: messages.ready,
          handler: () => this.resetForm(),
        },
      ],
    });
  }

  private resetForm(): void {
    this.viewModel.form.reset();
  }

  private async loadAccountTypeOptions() {
    const accountTypesMessages = this.translateProvider.get(
      LinkBankAccountConfig.i18n.accountTypes
    );

    this.viewModel.accountTypeOptions = [
      {
        alias: AccountType.SAVINGS,
        name: accountTypesMessages[AccountType.SAVINGS],
      },
      {
        alias: AccountType.CURRENT,
        name: accountTypesMessages[AccountType.CURRENT],
      },
    ];
  }
}
