import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AccountType, BankAccountEntity } from '@nequi/features/commons/core';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { BankRechargeLimitsEntity } from '@nequi/features/send/core';
import { MaskSensitivePipe, MaskType } from '@nequi/ui';

import { HomeConfig } from './home.config';

interface BankAccountViewModel extends BankAccountEntity {
  maskedNumber: string;
  visibleType: string;
  visibleName: string;
}

export class HomeViewModel {
  public form: FormGroup;
  public account: FormControl;
  public amount: FormControl;
  public accountTypeOptions = [];
  public limits: BankRechargeLimitsEntity;

  public accounts: BankAccountViewModel[];

  constructor(
    private sensitivePipe: MaskSensitivePipe,
    private translateProvider: TranslateProvider
  ) {}

  public get selectedAccount(): BankAccountViewModel {
    return this.account.value;
  }

  public get linkedAccounts(): BankAccountEntity[] {
    return this.accounts?.filter((account) => account.isLinked) ?? [];
  }

  public get hasAccountsToLink(): boolean {
    return this.accounts?.some((account) => !account.isLinked);
  }

  public async initForm(): Promise<void> {
    this.account = new FormControl('', [Validators.required]);
    this.amount = new FormControl(null, [Validators.required]);
    this.form = new FormGroup({
      account: this.account,
      amount: this.amount,
    });
  }

  public async setAccounts(accounts: BankAccountEntity[]): Promise<void> {
    this.accounts = (await this.mapAccounts(accounts)) ?? [];

    if (this.linkedAccounts.length === 1) {
      this.account.setValue(this.accounts[0]);
    }
  }

  public setLimits(limits: BankRechargeLimitsEntity): void {
    this.limits = limits;

    this.amount.setValidators([
      Validators.required,
      Validators.min(HomeConfig.minAmount),
      Validators.max(limits.daily),
    ]);
  }

  private async mapAccounts(accounts?: BankAccountEntity[]): Promise<BankAccountViewModel[]> {
    const messages = this.translateProvider.get(HomeConfig.accountTypesMessages);
    const accountTypes = {
      [AccountType.SAVINGS]: messages.savings,
      [AccountType.CURRENT]: messages.current,
    };

    return accounts?.map((account) => {
      const visibleType = accountTypes[account.type];
      const maskedNumber = this.sensitivePipe.transform(account.number, MaskType.BANK_ACCOUNT);

      return {
        ...account,
        visibleType,
        maskedNumber,
        visibleName: `${maskedNumber} - ${visibleType}`,
        otherValue: account.isLinked,
      };
    });
  }
}
